<template>
  <div>
    <div class="container">
      <h1>Aseguradora</h1>
    </div>
    <insurance-carrier-table
        :data="insuranceCarrierListState"
        @DeleteInsuranceCarrier="DeleteInsuranceCarrier"
        @addInsuranceCarrier="addInsuranceCarrier"
        @updateInsuranceCarrier="updateInsuranceCarrier"
    />
  </div>
</template>

<script>

import InsuranceCarrierTable from "@/components/insuranceCarrier/InsuranceCarrierTable";
import {mapActions, mapGetters} from "vuex";
export default {
  name: "InsuranceCarrier",
  components: {
    InsuranceCarrierTable
  },
  data () {
    return {
      jsonDataUpdate: {
        address: "",
        code: 0,
        contact: "",
        dirResponsable: "",
        id: 0,
        name: "",
        responsableFae: "",
        telResponsable: "",
        telephone: "",
        timesTamp: ""
      }
    }
  },
  created() {
    this.getInsuranceCarrierState()
  },
  computed: {
    ...mapGetters(['insuranceCarrierListState'])
  },
  methods: {
    ...mapActions(['getInsuranceCarrierState', 'setInsuranceCarriernew', 'apiUpdateInsuranceCarrier', 'apiDeleteInsuranceCarrier']),

    addInsuranceCarrier( item) {
      this.setInsuranceCarriernew(item)
    },
    updateInsuranceCarrier( item) {
      this.jsonDataUpdate.address = item.address;
      this.jsonDataUpdate.code = item.code;
      this.jsonDataUpdate.contact = item.contact;
      this.jsonDataUpdate.dirResponsable = item.dirResponsable;
      this.jsonDataUpdate.id = item.id;
      this.jsonDataUpdate.name = item.name;
      this.jsonDataUpdate.responsableFae = item.responsableFae;
      this.jsonDataUpdate.telResponsable = item.telResponsable;
      this.jsonDataUpdate.telephone = item.telephone;
      this.jsonDataUpdate.timesTamp = item.timesTamp;
     
      this.apiUpdateInsuranceCarrier(this.jsonDataUpdate)
    },
    DeleteInsuranceCarrier(id) {
      this.apiDeleteInsuranceCarrier(id)
    }
  }
}
</script>

